  animation-delay: 150ms;
}

.typing .dot:nth-child(3) {
  animation-delay: 300ms;
}

@keyframes typing {
  50%, 100%, 0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-150%);
  }
}

.circle {
  border-radius: 9999px !important;
}

.hamburger {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 0.5rem;
  cursor: pointer;
  height: 2.25rem;
  width: 2.25rem;
  transition: background-color 300ms linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  border-radius: 0.25rem;
  margin: 0;
  overflow: visible;
  outline: 0;
}

.hamburger-sm .hamburger-inner {
  transform: scale(0.8);
}

.hamburger:hover, .hamburger:focus, .hamburger.focus {
  background-color: rgba(255, 255, 255, 0.05);
  outline: 0;
}

.hamburger:active {
  background-color: rgba(20, 20, 31, 0.05);
}

.hamburger-box {
  width: 1.25rem;
  height: 1.25rem;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -1px;
}

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  width: 1.25rem;
  height: 2px;
  background-color: #fff;
  border-radius: 2px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::before, .hamburger-inner::after {
  content: "";
  display: block;
}

.hamburger-inner::before {
  top: -6px;
}

.hamburger-inner::after {
  bottom: -6px;
}

.hamburger-light .hamburger-inner,
.hamburger-light .hamburger-inner::before,
.hamburger-light .hamburger-inner::after {
  background-color: #222230;
}

/*
 * Arrow
 */
.hamburger-arrow.active .hamburger-inner::before {
  transform: translate3d(-0.25rem, 1px, 0) rotate(-45deg) scale(0.7, 1);
}

.hamburger-arrow.active .hamburger-inner::after {
  transform: translate3d(-0.25rem, -1px, 0) rotate(45deg) scale(0.7, 1);
}

/*
 * Squeeze
 */
.hamburger-squeeze .hamburger-inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger-squeeze .hamburger-inner::before {
  transition: top 0.075s 0.12s ease, opacity 0.075s ease;
}

.hamburger-squeeze .hamburger-inner::after {
  transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger-squeeze.active .hamburger-inner {
  transform: rotate(45deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger-squeeze.active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.075s ease, opacity 0.075s 0.12s ease;
}

.hamburger-squeeze.active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

body {
  background-color: #ffffff;
}

